der-sort;\n      }\n    }\n  }\n}\n\n@media screen and (min-width: map-get($grid-breakpoints, \"sm\")) {\n  body.rails_admin {\n    .btn-toggle-nav {\n      position: fixed;\n      top: $nav-link-height + $navbar-padding-y * 2;\n      bottom: 0;\n      overflow-y: auto;\n    }\n  }\n}\n\n@media screen and (max-width: map-get($grid-breakpoints, \"sm\")) {\n  body.rails_admin {\n    .container {\n      padding: 0;\n    }\n  }\n}\n",".ui-widget {\n  font-family: Arial, sans-serif;\n  font-size: 1.1em;\n}\n.ui-widget .ui-widget {\n  font-size: 1em;\n}\n.ui-widget input,\n.ui-widget select,\n.ui-widget textarea,\n.ui-widget button {\n  font-family: Arial, sans-serif;\n  font-size: 1em;\n}\n.ui-widget-content {\n  border: 1px solid #b6b6b6;\n  background: #ffffff;\n  color: #4f4f4f;\n}\n.ui-widget-content a {\n  color: #4f4f4f;\n}\n\n.ui-menu {\n  list-style: none;\n  padding: 2px;\n  margin: 0;\n  display: block;\n  float: left;\n}\n.ui-menu .ui-menu {\n  margin-top: -3px;\n}\n.ui-menu .ui-menu-item {\n  margin: 0;\n  padding: 0;\n  zoom: 1;\n  float: left;\n  clear: left;\n  width: 100%;\n}\n.ui-menu .ui-menu-item a {\n  text-decoration: none;\n  display: block;\n  padding: 0.2em 0.4em;\n  line-height: 1.5;\n  zoom: 1;\n}\n.ui-menu .ui-menu-item a.ui-state-hover,\n.ui-menu .ui-menu-item a.ui-state-active {\n  font-weight: normal;\n  margin: -1px;\n  background: #5f83b9;\n  color: #ffffff;\n  text-shadow: 0px 1px 1px #234386;\n  border-color: #466086;\n  border-radius: 0;\n}\n\n.ui-autocomplete {\n  position: absolute;\n  cursor: default;\n  z-index: 1056 !important;\n  border-radius: 0;\n  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.3);\n}\n\n.ui-helper-hidden-accessible {\n  display: none;\n}\n",".ra-multiselect {\n  .ra-multiselect-header {\n    margin-bottom: 5px;\n    clear: right;\n    .help {\n      float: left;\n      font-size: 10px;\n    }\n    .ui-icon {\n      position: relative;\n      top: 10px;\n      left: 3px;\n    }\n  }\n\n  .ra-multiselect-search {\n    wid